"use strict";

/* Button back top */
const radioTablist = (() => {
    //Check to see if the window is top if not then display button
    $(':radio').change(function (event) {
        var id = $(this).data('id');
        $('#' + id).removeClass('d-none').addClass('d-block');
        $('#' + id).siblings().removeClass('d-block').addClass('d-none');
    });
})();

export default radioTablist;


