"use strict";

/* Slide config */
const owlCarousel = (() => {
    // Slide highlights hotel & tour
    $(".js3ItemOwlCarousel").owlCarousel({
        loop: true,
        autoplay: true,
        margin: 30,
        responsive: {
            0: {
                items: 1
            },
            768: {
                items: 2
            },
            992: {
                items: 3
            }
        },
        nav: true,
        navText: [' <img class="" src="images/ic-sl.png" alt="">','<img class="" src="images/ic-sr.png" alt="">']
    })

    $(".js4ItemOwlCarousel").owlCarousel({
        loop: true,
        autoplay: true,
        margin: 20,
        responsive: {
            0: {
                items: 1
            },
            576: {
                items: 2
            },
            992: {
                items: 4
            }
        },
        nav: true,
        navText: [' <img class="" src="images/ic-sl.png" alt="">','<img class="" src="images/ic-sr.png" alt="">']
    })

    $('.owl-one').owlCarousel({
        items:1,
        loop:true,
        center:true,
        URLhashListener:true,
        autoplayHoverPause:true,
        autoHeight:true,
        startPosition: 'URLHash',
    })

    $('.owl-two').owlCarousel({
        items:5,
        loop:false,
        nav: true,
        margin:5,
        responsiveClass:true,
        responsive: {
            0: {
                items: 3
            },
            600: {
                items: 4
            },
            992: {
                items: 5
            }
        },
        navText: ["<img src='images/ic-sl1.svg'>","<img src='images/ic-sr1.svg'>"],
        
    })
})();

export default owlCarousel;


